import React, { useState, useEffect } from 'react';

import Head from 'next/head'
import LangPopUp from '../components/LangPopUp';
import Welcome from '../components/Welcome';
import About from '../components/About';
import Contact from '../components/Contact';
import { storeLang, getLang } from '../utils/lang';
import { getData } from '../utils/data';
import Work from '../components/Work';
import Navbar from '../components/Navbar';

const Home = () => {
  const [lang, setLang] = useState(false);

  useEffect(() => {
    // alert("Website Under Construction!");
    if (typeof window.localStorage.lang !== "undefined") {
      setLang(window.localStorage.lang);
    }
    () => {

      (function () {

        window.ldfdr = window.ldfdr || {};
        (function (d, s, ss, fs) {
          fs = d.getElementsByTagName(s)[0];

          function ce(src) {
            var cs = d.createElement(s);
            cs.src = src;
            setTimeout(function () { fs.parentNode.insertBefore(cs, fs) }, 1);
          }

          ce(ss);
        })(document, 'script', 'https://sc.lfeeder.com/lftracker_v1_lYNOR8xe9Gq7WQJZ.js');
      })();
    }
  }, []);

  const handleLang = (lang) => {
    setLang(lang);
    storeLang(lang);
  }

  const data = getData(lang);
  return (
    <div className="container" id="top">
      <Head>
        <title>(Under Construction) Ashraf El ghinoussi's Portfolio</title>
        <link rel="icon" href="/favicon.ico" />
      </Head>
      {(1 ? "" : <LangPopUp setLang={handleLang} data={data["language"]} />)}
      <Welcome setLang={handleLang} data={data["welcome"]} />
      <Navbar />
      <About />
      <Contact />
      <Work />
      <style jsx global>{`
      html,
      body {
        padding: 0;
        margin: 0;
        font-family: 'Raleway', -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen,
          Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
                    font-weight: 400;
      }

      * {
        box-sizing: border-box;
      }
      
    `}</style>
    </div>
  );
}

export default Home
